//  :        -
// : constants

import {
  TASK_TYPES,
  PRIORITIES,
  TRANSPORT_TYPES,
  PAYMENT_METHODS,
  TASK_STATUSES,
} from './constants';

export interface TaskField {
  name: string;
  label: string;
  type: string;
  required?: boolean;
  options?: readonly string[];
  default?: string;
}

export const taskFields: TaskField[] = [
  { name: 'title', label: '', type: 'text', required: true },
  {
    name: 'task_type',
    label: '',
    type: 'select',
    options: TASK_TYPES,
    default: TASK_TYPES[0],
  },
  {
    name: 'priority',
    label: '',
    type: 'select',
    options: PRIORITIES,
    default: PRIORITIES[1],
  },
  { name: 'creator', label: ' ', type: 'select' },
  { name: 'assignees', label: '', type: 'multiselect' },
  { name: 'start_location', label: ' ', type: 'location' },
  {
    name: 'transport_type',
    label: ' ',
    type: 'select',
    options: TRANSPORT_TYPES,
    default: TRANSPORT_TYPES[0],
  },
  { name: 'cargo_length_m', label: ', ', type: 'number' },
  { name: 'cargo_width_m', label: ', ', type: 'number' },
  { name: 'cargo_height_m', label: ', ', type: 'number' },
  { name: 'cargo_volume_m3', label: ', ', type: 'number' },
  { name: 'cargo_weight_kg', label: ', ', type: 'number' },
  { name: 'end_location', label: ' ', type: 'location' },
  {
    name: 'payment_method',
    label: ' ',
    type: 'select',
    options: PAYMENT_METHODS,
    default:
      (PAYMENT_METHODS as readonly string[]).find((method) =>
        /^ $/i.test(method.trim()),
      ) || PAYMENT_METHODS[0],
  },
  {
    name: 'payment_amount',
    label: '',
    type: 'number',
    default: '0',
  },
  {
    name: 'status',
    label: '',
    type: 'select',
    options: TASK_STATUSES,
    default: TASK_STATUSES[0],
  },
  { name: 'description', label: ' ', type: 'richtext' },
  { name: 'comment', label: '', type: 'richtext' },
];

export default taskFields;
